@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
body
{
  background-image:linear-gradient(to right bottom,#2193b0,#6dd5ed);
  background-attachment:fixed;
  color:#354538;
  font-family:"Times New Roman";
}
::-webkit-scoller
{
  width:10px;
}
::-webkit-scroller-thumb
{
  background-image:linear-gradient(to right bottom,#2193b0,#6dd5ed);
}
a
{
  text-decoration:none;
  color:blue;
}
a:hover
{
  text-decoration:none;
  color:black;
}
.app_container
{
  padding-top:50px;
}
.app_row
{
  height:86vh;
}
.sidebar
{
  background-color:white;
  height:100%;
  border-radius:20px;
  padding:25px 15px;
  box-shadow:0 0 10px black;
  text-align:center;
}
.app_main-content
{
  background-color:white;
  border-radius:20px;
  padding:25px 15px;
  box-shadow:0 0 10px black;
}
.sidebar_avatar
{
  height:110px;
  width:120px;
  object-fit:contain;
  border-radius:30px;
}
.sidebar_icon
{
  width:25px;
  height:25px;
}
.sidebar_name
{
  font-family:Kaushan Script;
  margin:20px 0;
  font-size:1.5rem;
}
.sidebar_name>span
{
  color:#2193b0;
}
.sidebar_item
{
  margin-bottom:6px;
  padding-top:6px;
  height:40;
  font-size:1.05rem;
  border-radius:30px;
}
.sidebar_title
{
  background-color:#ADD8E6;
  border-radius:30px;
}
.sidebar_contact
{
  background-color:#dbdddf;
  padding:2px 0;
  margin:20px -15px;
}
.sidebar_email
{
  background-image:linear-gradient(to right bottom,#2193b0,#6dd5ed);
  border-radius:30px;
  color:#fff;
  margin:15px 25px 0px;
  cursor:pointer; 
  transition:all 3s linear;
}
.sidebar_email:hover
{
  transform:scale(1.2);
}
.sidebar_resume:hover
{
  background-color:#f1f1f2;
  color:#000;
}
@media(max-width:991px)
{
  .app_main-content
  {
    margin:30px 0;
  }
}
/*Navbar*/
.navbar_items
{
  display:flex;
}
.navbar_active
{
  font-size:2rem;
  font-weight:700;
  color:#354358;
  border-bottom:4px solid #00F260;  
}
.navbar_item
{
  margin-right:20px;
  text-decoration:none;
  color:#354358;
  font-size:1.3rem;
  cursor:pointer; 
}
.navbar_item:hover
{
  background:-webkit-linear-gradient(#2193b0,#6dd5ed);
  background-clip:text;
  -webkit-background-clip:text;
  -webkit-text-fill-color:transparent;
}
/*about*/
.about
{
  padding:1.1rem;
  margin:-35px;
}
.about_intro
{
  font-size:1.1rem;
  padding:20px 28px;
  margin:10px 0;
}
.about_container
{
  background-color:#dbdddf;
  padding:30px 20px;
}
.about_heading
{
  font-size:25px;
  font-weight:700;
  color:#354358;
  margin-left:10px;
}
/*skillcard*/
.skill-card
{
  background-color:#fff;
  margin:10px;
  padding:10px;
  display:flex;
  flex-direction:row;
  border-radius:10px;
}
.skill-card_icon
{
  width:40px;
  height:40px;
}
.skill-card-body
{
  margin-left:15px;
}
.skill-card_content
{
  margin:0;
  font-size:0.9rem;
  color:blue;
}
/*resume*/
.resume
{
  padding-top:20px;
}
.resume-card_heading,
.resume-language_heading,
.resume-tool_heading
{
  margin-left:10px;
  font-size:1.6rem;
  font-weight:700;
  color:lightblue;
}
.resume-card_body
{
  margin-top:10px;
}
.resume-card_title
{
  font-weight:700;
}
.resume-card_name
{
  font-size:16px;
}
.resume-card_details
{
  font-size:0.9rem;
  padding-top:20px;
}
/*bar*/
.bar
{
  width:90%;
  background-color:#f1f2f3;
  border-radius:20px;
  margin-bottom:12px
}
.bar_icon
{
  width:15px;
  height:15px;
}
.bar_wrapper
{
  background-image:linear-gradient(90deg,#2193b0,#6dd5ed);
  border-radius:20px;
  padding:4px 14px;
  color:#fff;
  font-size:0.9rem;
}
/*projects*/
.projects
{
  height:70vh;
  padding-top:30px;
  overflow-y:auto;
}
.ProjectCard_wrapper
{
  margin:10px 0;
  width:100%;
  height:100px;
  min-height:160px;
}
.ProjectCard_image
{
  width:600px;
  height:240%;
  border-radius:30px;
  border: 1px solid #00ff00;
  transition:transform 0.3s linear;
}
.ProjectCard_image:hover
{
  transform:scale(1.05);
}
.ProjectCard_icon
{
  width:25px;
  height:25px;
  margin-right:5px;
} 
.ProjectCard_title
{
  margin-top:12px;
  font-size:1rem;
  display:flex;
  align-items:center;
  justify-content:center;
}
.projects_navbar
{
  display:flex;
  margin-bottom:5px;
}
.projects_navbar>div
{
  margin:0 5px;
  font-size:0;
  cursor:pointer;
}
@media(max-width:600px)
{
  .app_main-content
  {
    margin-right:10px;
    margin-left:10px;
  }
  .navbar_active
  {
    font-size:1.5rem;
  }
  .navbar_item
  {
    font-size:1rem;
  }
}
